// node_modules/rx-player/src/main_thread/api/public_api.ts
import RxPlayer from 'rx-player';

import playerEvents from './const/playerEvents';
import playerState from './const/playerStates';
import { playerErrorType, ErrorCodeType } from './const/playerError';
import appEvents from '../../const/appEvents';
import Logger from '../../utils/logger';

RxPlayer.LogFormat = 'full'; // "standard", "full"
RxPlayer.LogLevel = 'NONE'; // "NONE", "ERROR", "WARNING", "INFO", "DEBUG"

class RxMediaPlayer {
    constructor(manifestUrl, playerConfig = {}, drmConfig = null) {
        this.tag = `RxPlayer-${RxPlayer.version}`;
        this.logger = new Logger(this.tag);
        this.uiEvents = appEvents || appEvents?.default;
        // Player Config
        this.manifestUrl = manifestUrl;
        this.playerConfig = playerConfig;
        this.drmConfig = drmConfig;
        // Player Info
        this.player = null;
        this.customEvent = new EventTarget();
    }

    initialize() {
        if (!this.playerConfig.videoElement) {
            throw new Error("videoElement must be provided in playerConfig");
        }
        this.player = new RxPlayer(this.playerConfig);
    }

    load() {
        if (!this.player) {
            throw new Error("Player not initialized. Call initialize() first.");
        }
        this.bindEvents();
        const loadOptions = {
            url: this.manifestUrl,
            transport: "dash",
            autoPlay: true,
        };
        if (this.drmConfig) {
            loadOptions.drmOptions = this.configureDRM(this.drmConfig);
        }
        try {
            const loadResult = this.player.loadVideo(loadOptions);
            if (loadResult && typeof loadResult.catch === 'function') {
                loadResult.catch((error) => {
                    this.logger.error('Error loading video:', error);
                });
            }
        } catch (error) {
            this.logger.error('Error loading video:', error);
        }
    }

    configureDRM(drmConfig) {
        const drmOptions = {};

        if (drmConfig.clearkey) {
            // Handle ClearKey DRM
            const clearkeyStr = drmConfig.clearkey;
            const [kid, key] = clearkeyStr.split('=');
            drmOptions.clearkey = {
                fallbackKeyIds: [kid],
                fallbackKeys: [key]
            };
        }

        if (drmConfig.widevine) {
            // Handle Widevine DRM
            drmOptions.widevine = {
                licenseServerUrl: drmConfig.widevine.licenseServerUrl,
                serverCertificateUrl: drmConfig.widevine.serverCertificateUrl,
                httpRequestHeaders: drmConfig.widevine.httpRequestHeaders || {}
            };
        }

        if (drmConfig.playready) {
            // Handle PlayReady DRM
            drmOptions.playready = {
                licenseServerUrl: drmConfig.playready.licenseServerUrl,
                httpRequestHeaders: drmConfig.playready.httpRequestHeaders || {}
            };
        }

        if (drmConfig.fairplay) {
            // Handle FairPlay DRM
            drmOptions.fairplay = {
                licenseServerUrl: drmConfig.fairplay.licenseServerUrl,
                serverCertificateUrl: drmConfig.fairplay.serverCertificateUrl,
                httpRequestHeaders: drmConfig.fairplay.httpRequestHeaders || {}
            };
        }
        // Add other DRM types as needed
        return drmOptions;
    }

    // Player Events
    handlePlayerStateChange(state) {
        this.logger.warn('Player State: ' + state);
        this.dispatchEvent(new CustomEvent(this.uiEvents.CURRENT_STATE, { detail: state }));
        switch (state) {
            case playerState.LOADING:
                this.logger.debug("A new content is currently loading");
                break;
            case playerState.LOADED:
                this.logger.debug("The new content is loaded and ready to be played");
                break;
            case playerState.PLAYING:
                this.logger.debug("The player is currently playing");
                break;
            case playerState.PAUSED:
                this.logger.debug("The player is currently paused");
                break;
            case playerState.BUFFERING:
                this.logger.debug("The player is paused while buffering new data");
                break;
            case playerState.FREEZING:
                this.logger.debug("The player is frozen");
                break;
            case playerState.SEEKING:
                this.logger.debug("The player is still seeking, waiting for new data");
                break;
            case playerState.STOPPED:
                this.logger.debug("No content is/will be playing");
                break;
            case playerState.ENDED:
                this.logger.debug("The player has reached the end of the content.");
                break;
            case playerState.RELOADING:
                this.logger.debug("The player is currently reloading the content");
                break;
            default:
                this.logger.debug("Unknown state:", state);
                break;
        }
    }

    handleError(error) { // error = { type, code, message, fatal }
        this.logger.error('RxPlayer error:', error);
        this.dispatchEvent(new CustomEvent(this.uiEvents.ERROR, { detail: error }));
        // Categorize error based on type or code
        if (error && error.type) {
            switch (error.type) {
                case playerErrorType.NETWORK_ERROR:
                    this.logger.error('Network error occurred:', error.message);
                    break;
                case playerErrorType.MEDIA_ERROR:
                    this.logger.error('Media error occurred:', error.message);
                    break;
                case playerErrorType.ENCRYPTED_MEDIA_ERROR:
                    this.logger.error('DRM error occurred:', error.message);
                    break;
                case playerErrorType.OTHER_ERROR:
                    this.logger.error('OTHER Error happened', error.message)
                default:
                    this.logger.error('Unknown error:', error.message);
            }
        } else {
            this.logger.error('Player error: ' + error.message);
        }
    }

    handleWarning(warning) {
        this.dispatchEvent(new CustomEvent(this.uiEvents.WARNING, { detail: warning }));
    }

    handlePositionChange(obj) {
        // obj = { position, duration, bufferGap, playbackRate, liveGap, maximumPosition, wallClockTime }
        this.dispatchEvent(new CustomEvent(this.uiEvents.POSITION_UPDATE, { detail: obj }));
    }

    handlePlay() {
        this.logger.info('hanldePlay Trigger ...');
    }
    handlePause() {
        this.logger.info('handlePause Trigger ...')
    }
    handleSeeking() {
        this.logger.warn('Seeking Playback .... ');
    }
    handleSeeked() {
        this.logger.info('Seek Complete');
    }
    getAudioTrackList(arr) {
        this.logger.info('BM: audioList:', JSON.stringify(arr));
        this.dispatchEvent(new CustomEvent(this.uiEvents.AUDIO_TRACKLIST, { detail: arr && arr.length ? arr[0].representations : null }));
    }
    getVideoTrackList(arr) {
        this.logger.info('BM: videoList:', playerEvents.VIDEO_TRACK_LIST, JSON.stringify(arr));
        this.dispatchEvent(new CustomEvent(this.uiEvents.VIDEO_TRACKLIST, { detail: arr && arr.length ? arr[0].representations : null }));
    }
    getTextTrackList(arr) {
        // this.logger.info('subtitleList:', this.uiEvents.VIDEO_TRACKLIST, JSON.stringify(arr));
        this.dispatchEvent(new CustomEvent(this.uiEvents.VIDEO_TRACKLIST, { detail: arr && arr.length ? arr[0].representations : null }));
    }
    handleAudioTrackChange(obj) {
        this.logger.info('Current Audio: ', JSON.stringify(obj));
        // this.dispatchEvent(new CustomEvent(this.uiEvents.AUDIO_TRACKLIST, { detail: obj && obj.representations ? obj.representations : null }));
    }
    handleVideoTrackChange(obj) {
        this.logger.info('BM: Current Video: ', playerEvents.VIDEO_TRACK_CHANGE, JSON.stringify(obj));
        // this.dispatchEvent(new CustomEvent(this.uiEvents.VIDEO_TRACKLIST, { detail: obj && obj.representations ? obj.representations : null }));
    }
    handleTextTrackChange(obj) {
        this.logger.info('Current Subtitle: ', JSON.stringify(obj));
        // this.dispatchEvent(new CustomEvent(this.uiEvents.TEXT_TRACKLIST, { detail: obj && obj.representations ? obj.representations : null }));
    }
    handleVideoRepresentationChange(obj) {
        this.logger.info('Current Video Representation: ', JSON.stringify(obj));
    }
    handleAudioRepresentationChange(obj) {
        this.logger.info('Current Audio Representation: ', JSON.stringify(obj));
    }
    getPeriodList(obj) {
        this.logger.info('Period List: ', JSON.stringify(obj));
    }
    handleVolumnChange(obj) {
        this.logger.info('Volumn Info: ', JSON.stringify(obj));
    }

    dispatchEvent(event) {
        if (this.player && Object.values(this.uiEvents).includes(event.type)) {
            this.customEvent.dispatchEvent(event);
        }
    }

    addEventListener(eventType, callback) {
        if (this.player) {
            if (Object.values(this.uiEvents).includes(eventType)) {
                this.customEvent.addEventListener(eventType, callback);
            } else {
                this.player.addEventListener(eventType, callback);
            }
        }
    }

    removeEventListener(eventType, callback) {
        if (this.player) {
            if (Object.values(this.uiEvents).includes(eventType)) {
                this.customEvent.removeEventListener(eventType, callback);
            } else {
                this.player.removeEventListener(eventType, callback);
            }
        }
    }

    bindEvents() {
        // Basic Events
        this.addEventListener(playerEvents.PLAYER_STATE_CHANGE, this.handlePlayerStateChange.bind(this));
        this.addEventListener(playerEvents.ERROR, this.handleError.bind(this));
        this.addEventListener(playerEvents.WARNING, this.handleWarning.bind(this));
        this.addEventListener(playerEvents.POSITION_UPDATE, this.handlePositionChange.bind(this));
        this.addEventListener(playerEvents.PLAY, this.handlePlay.bind(this));
        this.addEventListener(playerEvents.PAUSE, this.handlePause.bind(this));
        this.addEventListener(playerEvents.SEEKING, this.handleSeeking.bind(this));
        this.addEventListener(playerEvents.SEEKED, this.handleSeeked.bind(this));
        // Track selection events
        this.addEventListener(playerEvents.AUDIO_TRACK_LIST, this.getAudioTrackList.bind(this));
        this.addEventListener(playerEvents.VIDEO_TRACK_LIST, this.getVideoTrackList.bind(this));
        this.addEventListener(playerEvents.TEXT_TRACK_LIST, this.getTextTrackList.bind(this));
        this.addEventListener(playerEvents.AUDIO_TRACK_CHANGE, this.handleAudioTrackChange.bind(this));
        this.addEventListener(playerEvents.VIDEO_TRACK_CHANGE, this.handleVideoTrackChange.bind(this));
        this.addEventListener(playerEvents.TEST_TRACK_CHANGE, this.handleTextTrackChange.bind(this));
        // Representation selection events
        this.addEventListener(playerEvents.VIDEO_FRAME_CHANGE, this.handleVideoRepresentationChange.bind(this));
        this.addEventListener(playerEvents.AUDIO_FRAME_CHANGE, this.handleAudioRepresentationChange.bind(this));
        // Playback Information
        this.addEventListener(playerEvents.PERIOD_LIST, this.getPeriodList.bind(this));
        // this.addEventListener(playerEvents.STREAM_EVENTS, this.handleStreamEvent),
        // this.addEventListener(playerEvents.STREAM_SKIP, this.handleStreamSkip),
        this.addEventListener(playerEvents.VOLUME_CHANGE, this.handleVolumnChange.bind(this));
    }

    destroyEvents() {
        // Basic Events
        this.removeEventListener(playerEvents.PLAYER_STATE_CHANGE, this.handlePlayerStateChange.bind(this));
        this.removeEventListener(playerEvents.ERROR, this.handleError.bind(this));
        this.removeEventListener(playerEvents.WARNING, this.handleWarning.bind(this));
        this.removeEventListener(playerEvents.POSITION_UPDATE, this.handlePositionChange.bind(this));
        this.removeEventListener(playerEvents.PLAY, this.handlePlay.bind(this));
        this.removeEventListener(playerEvents.PAUSE, this.handlePause.bind(this));
        this.removeEventListener(playerEvents.SEEKING, this.handleSeeking.bind(this));
        this.removeEventListener(playerEvents.SEEKED, this.handleSeeked.bind(this));
        // Track selection events
        this.removeEventListener(playerEvents.AUDIO_TRACK_LIST, this.getAudioTrackList.bind(this));
        this.removeEventListener(playerEvents.VIDEO_TRACK_LIST, this.getVideoTrackList.bind(this));
        this.removeEventListener(playerEvents.TEXT_TRACK_LIST, this.getTextTrackList.bind(this));
        this.removeEventListener(playerEvents.AUDIO_TRACK_CHANGE, this.handleAudioTrackChange.bind(this));
        this.removeEventListener(playerEvents.VIDEO_TRACK_CHANGE, this.handleVideoTrackChange.bind(this));
        this.removeEventListener(playerEvents.TEST_TRACK_CHANGE, this.handleTextTrackChange.bind(this));
        // Representation selection events
        this.removeEventListener(playerEvents.VIDEO_FRAME_CHANGE, this.handleVideoRepresentationChange.bind(this));
        this.removeEventListener(playerEvents.AUDIO_FRAME_CHANGE, this.handleAudioRepresentationChange.bind(this));
        // Playback Information
        this.removeEventListener(playerEvents.PERIOD_LIST, this.getPeriodList.bind(this));
        // this.removeEventListener(playerEvents.STREAM_EVENTS, this.handleStreamEvent),
        // this.removeEventListener(playerEvents.STREAM_SKIP, this.handleStreamSkip),
        this.removeEventListener(playerEvents.VOLUME_CHANGE, this.handleVolumnChange.bind(this));
    }

    // Public API Methods
    getVersion() {
        return `RxPlayer:${RxPlayer.version}`;
    }
    
    play() {
        if (this.player) {
            this.player.play();
        }
    }

    pause() {
        if (this.player) {
            this.player.pause();
        }
    }

    resume() {
        if (this.player) {
            this.player.play();
        }
    }

    stop() {
        if (this.player) {
            this.player.stop();
        }
    }

    seek(position) {
        if (this.player) {
            return this.player.seekTo(position);
        }
    }

    position() {
        if (this.player) {
            return this.player.getPosition();
        }
        return null;
    }

    volume() {
        if (this.player) {
            return this.player.getVolume();
        }
    }

    mute() {
        if (this.player) {
            this.player.mute();
        }
    }

    unmute() {
        if (this.player) {
            this.player.unMute();
        }
    }

    isPaused() {
        if (this.player) {
            return this.player.isPaused();
        }
    }

    isMute() {
        if (this.player) {
            return this.player.isMute();
        }
    }

    isBuffering() {
        if (this.player) {
            return this.player.isBuffering();
        }
    }

    setVolume(volume = 0.5) {
        if (this.player) {
            return this.player.setVolume(volume && volume > 1 ? volume % 1 : volume);
        }
    }

    getWallClockTime() {
        if (this.player) {
            return this.player.getWallClockTime();
        }
        return null;
    }

    setPlaybackRate(rate = 1) {
        if (this.player) {
            this.player.setPlaybackRate(rate);
        } else if(!this.player.areTrickModeTracksEnabled()) {
            this.logger.warn("playback is not active for change the playbackRate");
        }
    }

    setVideoTrack(obj) {
        if (this.player && obj && obj.id) {
            this.logger.info("BM: Trying to set videoTrack: ", JSON.stringify(obj));
            const videoTracks = this.player.getAvailableVideoTracks(); 
            this.logger.info("BM: Available tracks:", JSON.stringify(videoTracks));

            this.player.setVideoTrack(videoTracks[0].id);
        }
    }

    setVideoResolution(obj) {
        if(this.player && obj && obj.id) {
            this.logger.info("BM: Trying to set videoTrack Resolution: ", JSON.stringify(obj));

            this.player.setVideoResolution(obj.id);
        }
    }

    setAudioTrack(obj) {
        if(this.player && obj && obj.id) {
            this.logger.info("BM: Trying to set audioTrack: ", JSON.stringify(obj));
            this.player.setAudioTrack(obj.id);
        }
    }

    setAudioResolution(obj) {
        if(this.player && obj && obj.id) {
            this.logger.info("BM: Trying to set audioTrack Resolution: ", JSON.stringify(obj));
            this.player.setAudioResolution(obj.id);
        }
    }

    setSubtitle(obj) {
        if(this.player && obj && obj.id) {
            this.player.setTextTrack(obj.id);
        }
    }

    getMinimumPosition() {
        if (this.player) {
            return this.player.getMinimumPosition();
        }
        return null;
    }

    getMaximumPosition() {
        if (this.player) {
            return this.player.getMaximumPosition();
        }
        return null;
    }

    getLivePosition() {
        if (this.player) {
            return this.player.getLivePosition();
        }
        return null;
    }

    duration() {
        if (this.player) {
            return this.player.getMediaDuration();
        }
        return null;
    }

    getError() {
        if (this.player) {
            return this.player.getError();
        }
        return null;
    }

    getVideoElement() {
        if (this.player) {
            return this.player.getVideoElement();
        }
        return null;
    }

    getAvailablePeriods() {
        if (this.player) {
            return this.player.getAvailablePeriods();
        }
        return null;
    }

    getCurrentPeriod() {
        if (this.player) {
            return this.player.getCurrentPeriod();
        }
        return null;
    }

    dispose() {
        if (this.player) {
            this.player.dispose();
            this.player = null;
        }
    }

    reload(options) {
        if (this.player) {
            return this.player.reload(options);
        }
    }
}

export default RxMediaPlayer;
