import appConfig from '../const/appConfig'

class Utils {
    static uiControls(htmlDom) {
        return {
            PLAYER_VERSION: htmlDom.getElementById(appConfig.PLAYER_VERSION),

            RESTART_BTN:    htmlDom.getElementById(appConfig.uiButton.RESTART),
            REWIND_BTN:     htmlDom.getElementById(appConfig.uiButton.REWIND),
            PLAYPAUSE_BTN:  htmlDom.getElementById(appConfig.uiButton.PLAYPAUSE),
            FARWARD_BTN:    htmlDom.getElementById(appConfig.uiButton.FARWARD),
            STOP_BTN:       htmlDom.getElementById(appConfig.uiButton.STOP),
            
            VIDEO_SEEKBAR:  htmlDom.getElementById(appConfig.videoPos.PROGRESSBAR),
            STARTTIME:      htmlDom.querySelector(appConfig.videoPos.CURRENTTIME),
            ENDTIME:        htmlDom.querySelector(appConfig.videoPos.DURATION),
            
            VOLUME_BAR:     htmlDom.getElementById(appConfig.uiVolumn.PROGRESSBAR),
            VOLUME_BTN:     htmlDom.getElementById(appConfig.uiVolumn.VOLUMN),

            PLAYBACK_SPEED: htmlDom.getElementById(appConfig.quality.PLAYBACKSPEED),
            SUBTITLE_TRACKS:htmlDom.getElementById(appConfig.quality.SUBTITLE_TRACKS),

            VIDEO_TRACKS:   htmlDom.getElementById(appConfig.quality.VIDEO_TRACKS),
            AUDIO_TRACKS:   htmlDom.getElementById(appConfig.quality.AUDIO_TRACKS),
            VIDEO_RESOLUTION_LIST: htmlDom.getElementById(appConfig.quality.VIDEO_QUALITY_LIST),
            AUDIO_RESOLUTION_LIST: htmlDom.getElementById(appConfig.quality.AUDIO_QUALITY_LIST),
        }
    }

    static formatTime(seconds) {
        const hrs = Math.floor(seconds / 3600);
        const mins = Math.floor((seconds % 3600) / 60);
        const secs = Math.floor(seconds % 60);
        return [hrs, mins, secs]
            .map((v) => v < 10 ? '0' + v : v)
            .join(':');
    }

    static seekBarPercent(currentTime, duration) {
        return Number(((currentTime / duration) * 100).toFixed(2));
    }

    static seekBarPercentToTime(progressBarVal = 0, duration) {
        return (progressBarVal / 100) * duration;
    }

    static bitrateToQuality(bitrate) {
        if (bitrate >= 4000000) return '1080p';
        if (bitrate >= 2500000) return '720p';
        if (bitrate >= 1000000) return '480p';
        if (bitrate >= 500000) return '360p';
        if (bitrate >= 250000) return '240p';
        return '144p';
    }

    static bitrateToMbps(bitrate) {
        return `${(bitrate / 1000000).toFixed(2)} Mbps`;
    }
    static bitrateToKbps(bitrate) {
        return `${(bitrate / 1000).toFixed(2)} Kbps`;
    }
    static isSafari() {
        return /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
    }
}

export default Utils;
