// https://developers.canal-plus.com/rx-player/versions/4.3.0/doc/api/Player_Events.html

const playerEvents = {
    // Basic events
    PLAYER_STATE_CHANGE: "playerStateChange", // Emit the current state of the player, every time it changes
    ERROR: "error",  // Triggered when a fatal error happened.
    WARNING: "warning", // Triggered each time a minor error happened.
    POSITION_UPDATE: "positionUpdate", // Emit information about the current position at most every seconds 
    PLAY: "play", // Emitted when videoElement is no longer considered paused.
    PAUSE: "pause", // Emitted when videoElement is now considered paused.
    SEEKING: "seeking", // Emitted when a "seek" operation (to "move"/"skip" to another position) begins
    SEEKED: "seeked", // Emitted when a "seek" operation (to "move"/"skip" to another position) on the currently loaded content has finished

    // Track selection events
    AUDIO_TRACK_LIST: "availableAudioTracksChange", // Triggered when the currently available audio tracks might have changed (e.g.: at the beginning of the content, when period changes...) 
    VIDEO_TRACK_LIST: "availableVideoTracksChange", // Triggered when the currently available video tracks might change (e.g.: at the beginning of the content, when period changes...) 
    TEXT_TRACK_LIST: "availableTextTracksChange", // Triggered when the currently available text tracks might change (e.g.: at the beginning of the content, when period changes...)
    AUDIO_TRACK_CHANGE: "audioTrackChange", // Information about the current audio track, each time it changes for the currently-playing Period
    VIDEO_TRACK_CHANGE: "videoTrackChange", // Information about the current video track, each time it changes for the currently-playing Period.
    TEST_TRACK_CHANGE: "textTrackChange", // Information about the current text track, each time it changes for the currently-playing Period.

    // Representation selection events
    VIDEO_FRAME_CHANGE: "videoRepresentationChange", // Emitted when the current video Representation being considered by the RxPlayer changes.
    AUDIO_FRAME_CHANGE: "audioRepresentationChange", // Emitted when the current audio Representation being considered by the RxPlayer changes.
    REPRESENTATIONLISTUPDATE: "representationListUpdate", // Event triggered if the list of available Representation linked to the currently-chosen video, audio or text track for any Period 
    BROKENREPRESENTATIONSLOCK: "brokenRepresentationsLock", // Fairly rare event triggered if representations locked through Representations selection API such as lockVideoRepresentations or lockAudioRepresentations all became unplayable (most likely linked to encryption reasons), in which case, the RxPlayer "broke" that lock, i.e. it decided to remove that lock and play all Representations instead.

    // Playback information
    PERIODCHANGE: "periodChange", // Triggered when the current Period being seen changes.
    PERIOD_LIST: "newAvailablePeriods", // This event is triggered when one or multiple new Periods start to be considered by the RxPlayer in the current content.
    TRACKUPDATE: "trackUpdate", // Event triggered if a video, audio or text track chosen for any Period is changed by the RxPlayer.

    // Inband events
    INBANDEVENTS: "inbandEvents", // Event triggered when the player encounters inband events in the stream. These events are included in the loaded and parsed chunks, and are often used to carry content metadata.
    STREAMEVENT: "streamEvent", // Event triggered when the player enters the time boundaries of a "stream event".
    STREAMEVENTSKIP: "streamEventSkip", // Event triggered when the player skipped the time boundaries of a "stream event" (you can refer to the streamEvent event for a definition of what a "stream event" is).

    // Volume events
    VOLUME_CHANGE: "volumeChange" // Notify about a change of audio volume and/or of muted status.
};

export default playerEvents;
