import '../css/style.css';
import mediaList from '../config/assetInfo';
import { OTTMediaPlayer } from './solutions';
import Utils from './utils/utils';
import appConfig from './const/appConfig';
import appEvents from './const/appEvents';
import Logger from './utils/logger';
import appState from './const/appState';

class App {
    constructor() {
        this.tag = `App`;
        this.logger = new Logger(this.tag);
        this.assetSelect = document.getElementById('asset-select');
        this.populateAssetSelect();
        this.loadSelectedStream(mediaList.dash[0])
    }

    loadSelectedStream(selectedStreamInfo = null) {
        this.stream = selectedStreamInfo ? selectedStreamInfo : this.stream;
        this.manifestUrl = this.stream.manifestURL;
        this.drmConfig = this.stream.drmOptions || null;
        this.playerConfig = {
            videoElement: document.getElementById('video')
        };

        // Initialize the player
        this.myPlayer = new OTTMediaPlayer(this.manifestUrl, this.playerConfig, this.drmConfig);
        // UI Player Inputs
        this.uiButtons = Utils.uiControls(document);
        this.updatePlayerInfo(this.myPlayer.getVersion());
        // Load the player
        this.myPlayer.initialize();
        this.myPlayer.load();
        
        this.bindEvents();
    }

    updatePlayerInfo(playerVersion) {
        this.uiButtons.PLAYER_VERSION.textContent = playerVersion;
        const playerVersionLink = this.uiButtons.PLAYER_VERSION.closest('a');
        if (playerVersionLink) {
            playerVersionLink.href = "https://developers.canal-plus.com/rx-player/versions/4.3.0/doc/api/Overview.html";
            playerVersionLink.title = "View RxPlayer Documentation";
        }
    }

    populateAssetSelect() {
        // Clear existing options
        this.assetSelect.innerHTML = '';
        const mediaInfo = Utils.isSafari() ? mediaList.hls : mediaList.dash;

        mediaInfo.forEach((asset, index) => {
            const option = document.createElement('option');
            option.value = `dash-${index}`;
            option.text = `DASH - ${asset.title || asset.manifestURL}`;
            if (index === 0) option.selected = true; // Select the first DASH asset by default
            this.assetSelect.appendChild(option);
        });
    }

    bindEvents() {
        // Player Controls Buttons
        this.uiButtons.RESTART_BTN.addEventListener('click', () => this.resetPlayback());
        this.uiButtons.REWIND_BTN.addEventListener('click', () => this.rewind());
        this.uiButtons.PLAYPAUSE_BTN.addEventListener('click', () => this.togglePlayPause());
        this.uiButtons.FARWARD_BTN.addEventListener('click', () => this.fastForward());
        this.uiButtons.STOP_BTN.addEventListener('click', () => this.stopPlayback());
        this.uiButtons.PLAYBACK_SPEED.addEventListener('change', () => this.changePlaybackSpeed());
        // Volumn Buttons
        this.uiButtons.VOLUME_BAR.addEventListener('input', () => this.changeVolume());
        this.uiButtons.VOLUME_BTN.addEventListener('click', () => this.toggleMute());
        // Video-Audio Quality & speed
        this.uiButtons.VIDEO_TRACKS.addEventListener('change', () => this.changeVideoType());
        this.uiButtons.AUDIO_TRACKS.addEventListener('change', () => this.changeAudioType());
        this.uiButtons.VIDEO_RESOLUTION_LIST.addEventListener('change', () => this.changeVideoResolution());
        this.uiButtons.AUDIO_RESOLUTION_LIST.addEventListener('change', () => this.changeAudioResolution());
        this.uiButtons.SUBTITLE_TRACKS.addEventListener('change', () => this.changeSubtitleTrack());
        // Player Position & SeekBar
        this.uiButtons.VIDEO_SEEKBAR.addEventListener('input', () => this.seekVideo());
        // Asset Selection
        this.assetSelect.addEventListener('change', () => this.changeAsset());

        // Listen to custom appEvents from Solution Player class
        this.myPlayer.customEvent.addEventListener(appEvents.POSITION_UPDATE, (event) => this.updateProgress(event.detail));
        this.myPlayer.customEvent.addEventListener(appEvents.AUDIO_TRACKLIST, (event) => this.updateDropdowns(appConfig.quality.AUDIO_QUALITY_LIST, event.detail));
        this.myPlayer.customEvent.addEventListener(appEvents.VIDEO_TRACKLIST, (event) => this.updateDropdowns(appConfig.quality.VIDEO_QUALITY_LIST, event.detail));
        this.myPlayer.customEvent.addEventListener(appEvents.SUBTITLE_TRACKS, (event) => this.updateDropdowns(appConfig.quality.SUBTITLE_TRACKS, event.detail));

        this.myPlayer.customEvent.addEventListener(appEvents.CURRENT_STATE, (event) => this.checkState(event.detail));
        this.myPlayer.customEvent.addEventListener(appEvents.ERROR, (event) => {
            console.error('xx: Custom Event - Player Error:', event);
        });
        this.myPlayer.customEvent.addEventListener(appEvents.WARNING, (event) => {
            console.warn('xx: Custom Event - Player Warning:', event);
        });
    }

    destroy() {
        if(this.player) {
            this.player.destroy();
        }
    }

    togglePlayPause() {
        this.myPlayer.isPaused() ? this.myPlayer.play() : this.myPlayer.pause();
        this.updateButtonStates(appConfig.uiButton.PLAYPAUSE);
    }

    changeVolume() {
        this.myPlayer.setVolume(parseFloat(this.uiButtons.VOLUME_BAR.value));
        this.updateButtonStates(appConfig.uiVolumn.VOLUMN);
    }

    toggleMute() {
        this.myPlayer.isMute() ? this.myPlayer.unmute() : this.myPlayer.mute();
        this.updateButtonStates(appConfig.uiVolumn.VOLUMN);
    }

    resetPlayback() {
        this.myPlayer.seek(0);
        this.uiButtons.VIDEO_SEEKBAR.value = 0;
        // Reset CSS variable for progress fill
        this.uiButtons.VIDEO_SEEKBAR.style.setProperty('--progress', '0%');
        this.myPlayer.play();
        this.updateButtonStates(appConfig.uiButton.PLAYPAUSE);
    }

    rewind() {
        const currentTime = this.myPlayer.position();
        this.myPlayer.seek(Math.max(0, currentTime - 10));
    }

    fastForward() {
        const currentTime = this.myPlayer.position();
        const duration = this.myPlayer.duration();
        this.myPlayer.seek(Math.min(duration, currentTime + 10));
    }

    stopPlayback() {
        this.myPlayer.stop();
        this.updateButtonStates(appConfig.uiButton.PLAYPAUSE);
    }

    changePlaybackSpeed() {
        const speed = this.uiButtons.PLAYBACK_SPEED.value ? parseFloat(this.uiButtons.PLAYBACK_SPEED.value) : 1;
        this.myPlayer.setPlaybackRate(speed);
    }

    changeVideoType() {
        this.logger.info(`BM: Audio quality changed to ${this.uiButtons.VIDEO_TRACKS.value}`);
        // Implement actual video quality change logic if supported by player
        this.myPlayer.setVideoTrack({id: this.uiButtons.VIDEO_TRACKS.value});
    }

    changeVideoResolution() {
        this.logger.info(`Video resolution quality changed to ${this.uiButtons.VIDEO_RESOLUTION_LIST.value}`);
        this.player.setVideoResolution({id: this.uiButtons.VIDEO_RESOLUTION_LIST.value});   
    }
    
    changeAudioType() {
        this.logger.info(`Audio quality changed to ${this.uiButtons.AUDIO_TRACKS.value}`);
        // Implement actual audio quality change logic if supported by player
        this.myPlayer.setAudioTrack({id: this.uiButtons.AUDIO_TRACKS.value});
    }

    changeAudioResolution() {
        this.logger.info(`Audio resolution quality changed to ${this.uiButtons.AUDIO_RESOLUTION_LIST.value}`);
        this.player.setAudioResolution({id: this.uiButtons.AUDIO_RESOLUTION_LIST.value});
    }

    changeSubtitleTrack() {
        this.logger.info(`Subtitle quality changed to ${this.uiButtons.SUBTITLE_TRACKS.value}`);
        // Implement actual audio quality change logic if supported by player
        this.myPlayer.setSubtitle({id: this.uiButtons.SUBTITLE_TRACKS.value});
    }


    updateProgress(obj = { position: 0, duration: 0 }) {
        if (obj && obj.position !== undefined && obj.duration !== undefined) {
            this.uiButtons.STARTTIME.textContent = Utils.formatTime(obj.position);
            this.uiButtons.ENDTIME.textContent = Utils.formatTime(obj.duration);

            if (obj.duration > 0) {
                const progressPercent = Utils.seekBarPercent(obj.position, obj.duration);
                this.uiButtons.VIDEO_SEEKBAR.value = progressPercent;
                // Update CSS variable for red progress fill
                this.uiButtons.VIDEO_SEEKBAR.style.setProperty('--progress', progressPercent + '%');
            }
        }
    }

    seekVideo() {
        const duration = this.myPlayer.duration();
        if (duration > 0) {
            const seekTime = Utils.seekBarPercentToTime(this.uiButtons.VIDEO_SEEKBAR.value, duration);
            this.myPlayer.seek(seekTime);
        }
    }

    checkState(state) {
        this.logger.info('Player State Changed to:', state);
        switch (state) {
            case appState.LOADING:
            case appState.BUFFERING:
                // TODO: Show loading spinner
                break;
            case appState.LOADED:
                // TODO: Hide Loading spinner
                break;
            case appState.PLAYING:
            case appState.PAUSED:
                this.updateButtonStates(appConfig.uiButton.PLAYPAUSE);
                break;
            case appState.SEEKING:
                this.updateButtonStates(appConfig.uiButton.PLAYPAUSE);
                break;
            case appState.STOPPED:
                this.uiButtons.VIDEO_SEEKBAR.value = 0;
                // Reset CSS variable for progress fill
                this.uiButtons.VIDEO_SEEKBAR.style.setProperty('--progress', '0%');
                this.uiButtons.STARTTIME.textContent = '00:00';
                this.uiButtons.ENDTIME.textContent = '00:00';
                this.updateButtonStates(appConfig.uiButton.PLAYPAUSE);
                break;
            default:
                this.logger.error('Trigger Invalid App State: ', state);
                break;
        }
    }

    updateDropdowns(dropdownType, trackList) {
        let uiDropDownItem = null;
        switch (dropdownType) {
            case appConfig.quality.VIDEO_TRACKS:
                uiDropDownItem = this.uiButtons.VIDEO_TRACKS;
                break;
            case appConfig.quality.AUDIO_TRACKS:
                uiDropDownItem = this.uiButtons.AUDIO_TRACKS;
                break;
            case appConfig.quality.VIDEO_QUALITY_LIST:
                uiDropDownItem = this.uiButtons.VIDEO_RESOLUTION_LIST;
                break;
            case appConfig.quality.AUDIO_QUALITY_LIST:
                uiDropDownItem = this.uiButtons.AUDIO_RESOLUTION_LIST;
                break;
            case appConfig.quality.SUBTITLE_TRACKS:
                uiDropDownItem = this.uiButtons.SUBTITLE_TRACKS;
                break;
            default:
                break;
        }

        if(uiDropDownItem) {
            if (trackList && trackList.length) {
                uiDropDownItem.hidden = false;
                uiDropDownItem.innerHTML = '';
                trackList.forEach((track, index) => {
                    const option = document.createElement('option');
                    option.value = track.id;
                    option.text = `${Utils.bitrateToMbps(track.bitrate)} - ${track.codec}`;
                    uiDropDownItem.appendChild(option);
                });
            } else {
                uiDropDownItem.hidden = true;
                uiDropDownItem.innerHTML = '';
                const option = document.createElement('option');
                option.text = `No Video List`;
                uiDropDownItem.appendChild(option);
                this.logger.error('No Video list avaliable');
            }
        } else {
            this.logger.error('Invalid dropdown type specified:', dropdownType);
        }
    }

    updateButtonStates(buttonType) {
        switch (buttonType) {
            case appConfig.uiButton.PLAYPAUSE:
                this.uiButtons.PLAYPAUSE_BTN.querySelector('i').classList.remove(this.myPlayer.isPaused() ? 'fa-pause' : 'fa-play');
                this.uiButtons.PLAYPAUSE_BTN.querySelector('i').classList.add(this.myPlayer.isPaused() ? 'fa-play' : 'fa-pause');
                break;
            case appConfig.uiVolumn.VOLUMN:
                this.uiButtons.VOLUME_BTN.querySelector('i').classList.remove(this.myPlayer.isMute() ? 'fa-volume-up' : 'fa-volume-mute');
                this.uiButtons.VOLUME_BTN.querySelector('i').classList.add(this.myPlayer.isMute() ? 'fa-volume-mute' : 'fa-volume-up');
                break;
            default:
                break;
        }
    }

    changeAsset() {
        const selectedValue = this.assetSelect.value;
        if (!selectedValue) return;

        const [type, indexStr] = selectedValue.split('-');
        const index = parseInt(indexStr, 10);
        const mediaInfo = Utils.isSafari() ? mediaList.hls : mediaList.dash;
        this.stream = mediaInfo[index];

        if (!this.stream) {
            console.warn('Unknown asset type selected:', type);
            return;
        }
        if(this.player) {
            this.player.stop();
            this.destroy();
        }
        setTimeout(() => {
            this.loadSelectedStream()
        }, 2000);
    }
}

document.addEventListener('DOMContentLoaded', () => {
    new App();

    // Sidebar toggle button functionality
    const toggleSidebar = document.getElementById('toggle-sidebar');
    const container = document.querySelector('.container');

    setupSidebarToggle(toggleSidebar, container);

    // Seekbar tooltip functionality
    const progressSlider = document.getElementById('progress');
    const tooltip = document.getElementById('progress-tooltip');

    updateSeekbarTooltip(progressSlider, tooltip);

    // Toggle icon functionality
    const toggleIcons = document.querySelectorAll('.toggle-icon');

    toggleIcons.forEach(icon => {
        icon.addEventListener('click', () => {
            const isChecked = icon.dataset.checked === 'true';
            if (isChecked) {
                icon.className = 'fa fa-eye-slash toggle-icon';
                icon.dataset.checked = 'false';
            } else {
                icon.className = 'fa fa-eye toggle-icon';
                icon.dataset.checked = 'true';
            }
        });
    });
});

function updateSeekbarTooltip(progressSlider, tooltip) {
    function updateTooltip() {
        const value = progressSlider.value;
        const percentage = value + '%';
        tooltip.textContent = percentage;

        // Update progress bar fill
        progressSlider.style.setProperty('--progress', percentage);

        // Position the tooltip
        const sliderRect = progressSlider.getBoundingClientRect();
        const sliderWidth = sliderRect.width;
        const thumbPosition = (value / 100) * sliderWidth;
        tooltip.style.left = thumbPosition + 'px';
    }

    progressSlider.addEventListener('input', () => {
        updateTooltip();
        tooltip.style.opacity = '1';
    });

    progressSlider.addEventListener('mouseover', () => {
        updateTooltip();
        tooltip.style.opacity = '1';
    });

    progressSlider.addEventListener('mouseout', () => {
        tooltip.style.opacity = '0';
    });

    // Initial update
    updateTooltip();
}

function setupSidebarToggle(toggleSidebar, container) {
    toggleSidebar.addEventListener('click', () => {
        container.classList.toggle('left-hidden');
    });
}

