import appConfig from '../const/appConfig'
class Logger {
  constructor(tag = '') {
    this.tag = `[${tag}] `;
  }

  error(...text) {
    this.log(Logger.level.error, text);
  }
  warn(...text) {
    this.log(Logger.level.warn, text);
  }
  info(...text) {
    this.log(Logger.level.info, text);
  }
  debug(...text) {
    this.log(Logger.level.debug, text);
  }

  log(type, ...text) {
    if (Logger.levelKey[type] > Logger.levelKey[appConfig.LOG_LEVEL]) return;
    const msg = this.tag + (text[0] && text[0].length ? text[0].join(' ') : '');
    switch (type) {
      case Logger.level.error:
        console.error(msg);
        break;
      case Logger.level.warn:
        console.warn(msg);
        break;
      case Logger.level.info:
        console.info(msg);
        break;
      case Logger.level.debug:
        console.debug(msg);
        break;
      default:
        console.log(msg);
    }
  }
}

Logger.level = {
  none: 'none',
  error: 'error',
  warn: 'warn',
  info: 'info',
  debug: 'debug'
};

Logger.levelKey = {
  none: 0,
  error: 1,
  warn: 2,
  info: 3,
  debug: 4
};

export default Logger;
